ul{
  list-style: none;
  margin: 0;
  padding: 0;
}
.hidden{
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}
a{
  text-decoration: none;
}
img{
  max-width: 100%;
  height: auto;
}
